<template>
<div class="zhongBid">
  <v-fix :nav="2"></v-fix>
  <!-- 搜索框 -->
  <div class="search">
    <van-search placeholder="请输入搜索关键词" v-model="data.title" @search="searchFn"></van-search>
  </div>
  <!-- 筛选 -->
  <div class="screen-box">
    <div class="condition" :class="{'active':o.active}" v-for="(o,i) of screenList" :key="i" @click="showMask(i)">{{o.txt}}</div>
    <v-addr @addObj="returnAddress" v-if="screenList[0].active" :add="data.regions"></v-addr>
    <v-type @sureFn='typeSure' @canleFn="typeCanle" v-if="screenList[1].active"></v-type>
    <v-money @sureFn='moneySure' @canleFn="typeCanle" v-if="screenList[2].active" :isShow="true"></v-money>
  </div>
  <!-- 总条数 -->
  <div class="total">为您搜索到{{total}}条中标信息</div>
  <!-- 列表 -->
  <van-pull-refresh v-model="loading" @refresh="onRefresh">
    <van-list finished-text="没有更多了" @load="onLoad">
      <v-zb v-for="(o,i) of zbList" :key="i" :obj="o"></v-zb>
    </van-list>
  </van-pull-refresh>  
</div>
</template>
<script>
import zbCon from '@/components/zhongbCon'
import fixHead from '@/components/fixHead'
import search from '@/components/search'
import addr from '@/components/address'
import getType from '@/components/getType'
import money from '@/components/money'
export default {
    data () {
      return {
        zbList:[],
        loading:false,//是否加载完，false为加载完
        data:{
          pageNo:1,
          pageSize:'10',
          regions:'湖南',
          type: "2",
          projectType:'',
          title: "",
          projSumStart:'',//中标开始金额
          projSumEnd:'',//中标结束金额
          sumType:"zhongbiao"
        },
        total:0,
        screenList:[
          {
            txt:'地区',
            active:false,
          },{
            txt:'类型',
            active:false
          },{
            txt:'中标金额',
            active:false
          },
        ],
        moneyNum:0
      }
    },
    methods: {
      onRefresh(){//下拉刷新
        setTimeout(() => {
          this.data.pageNo=1;
          this.ajax();
        }, 500);
      },
      ajax(){
        //中标
        let that=this;
        this.$http({
            method:'post',
            url: '/notice/queryList',
            data:that.data
        }).then(function(res){
            that.loading = false;
            that.data.projSumStart='';
            that.data.projSumEnd='';
            that.total=res.data.total;
            if(that.zbList==0||that.data.pageNo==1){
              that.zbList=res.data.data;
            }else{
              that.zbList=that.zbList.concat(res.data.data)
            }
            
        })
      },
      onLoad(){//下滚加载
        this.data.pageNo++;
        this.ajax();
      },
      searchFn(){//搜索
        this.zbList=[];
        this.data.pageNo=1;
        this.ajax();
      },
      returnAddress(option){//选择地址
        this.zbList=[];
        this.screenList[0].active=false;
        this.screenList[0].txt=option;
        this.data.regions=option;
        this.data.pageNo=1;
        this.ajax();
      },
      typeSure(option){//类型选择
        this.zbList=[];
        this.screenList[1].active=false;
        this.data.projectType=option;
        this.data.pageNo=1;
        this.ajax();
      },
      showMask(i){//
        if(this.screenList[i].active){
          this.screenList[i].active=false
        }else{
          for(let x of this.screenList){
            x.active=false
          }
          this.screenList[i].active=true;
        }
      },
      typeCanle(){
        this.screenList[1].active=false;
        this.screenList[2].active=false;
      },
      moneySure(option){
          this.zbList=[];
          if(option.num==1){
                this.data.projSumEnd='500'
          }else if(option.num==2){
              this.data.projSumStart='500';
              this.data.projSumEnd='1000';
          }else if(option.num==3){
              this.data.projSumStart='1000';
              this.data.projSumEnd='5000';
          }else if(option.num==4){
              this.data.projSumStart='5000';
          }else{
              this.data.projSumStart=option.projSumStart;
              this.data.projSumEnd=option.projSumEnd;
          }
          this.screenList[2].active=false;
          this.data.pageNo=1;
          this.ajax();
      }
    },
    components:{
        'v-zb':zbCon,
        'v-fix':fixHead,
        'v-search':search,
        'v-addr':addr,
        'v-type':getType,
        'v-money':money,
    },
    created(){
      this.data.title = this.$route.query.search ?  this.$route.query.search : ''
    }
}
</script>
<style lang="less" scoped>
.zhongBid {
  padding-top: 200px;
  /*筛选*/
  .screen-box{
    position: relative;
    height: 80px;
    display: flex;
    background: #fff;
    align-items: center;
    font-size: 28px;
    padding: 0 32px;
    justify-content: space-between;
    .condition{
      display: flex;
      align-items: center;
    }
    .condition.active{
      color: #FE6603
    }
    .condition.active::after{
      content:'';
      width: 0;
      height: 0;
      margin-left: 12px;
      margin-bottom: 7px;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-bottom: 7px solid #FE6603;
      border-top: 7px solid transparent;
    }
    .condition::after{
      content:'';
      width: 0;
      height: 0;
      margin-left: 12px;
      margin-top: 7px;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-top: 7px solid #333;
      border-bottom: 7px solid transparent;
    }
  }
  /*总条数*/
  .total{
    font-size: 24px;
    color: #999;
    padding: 24px 32px;
    background: #f5f5f5
  }
}
</style>


